import React,{useState,useEffect} from 'react';
import Bio from './Bio';
import Edu from './Edu';
import Certs from './Certs';
import Exp from './Exp';
import Projects from './Projects';
import Skills from './Skills';
import Links from './Links';
import Fun from './Fun';

const Body = (props)=>{
  return (
    <div style={{paddingTop:'180px',paddingBottom:'180px'}}>
	{(props.page=='bio')&&<Bio/>}
	{(props.page=='links')&&<Links/>}	
	{(props.page=='experience')&&<Exp/>}	
	{(props.page=='projects')&&<Projects/>}	
	{(props.page=='skills')&&<Skills/>}	
    {(props.page=='education')&&<Edu/>}	
    {(props.page=='certificates')&&<Certs/>}	
    {(props.page=='fun')&&<Fun/>}	
    </div>
  );
}

export default Body;