import React,{useState,useEffect} from 'react';

const Header = (props)=>{
  const [name,setName] = useState('');
  const [head,setHead] = useState([]);
  const [lang,setLang] = useState(0);
  useEffect(()=>{
    const url ='https://spreadsheets.google.com/feeds/list/1EYJ9WPjqKqe26InIhdzyFKZzBKgAKRkM04w7aaFQhxk/od6/public/values?alt=json';
    fetch(url) 
    .then(res => res.json())
    .then(data=>data.feed.entry)
    .then(entry=>{
      let items = [];
      entry.forEach(e=>{
        let index = 'gsx$'+'head'
        let item = e[index]['$t'];
        if(item.length>0) items.push(item);
      });
      setName(items[props.lang]);
      setHead(items); 
      document.title=name;
    });
  });
  return(
    <>
    <div className="header f">
     <br/>
	   <h1>{name}</h1>
     <br/>
      <div id="subhead">
      <div>
      	<b>
        <a href="#bio" onClick={()=>{props.handlePage('bio')}}>{head[3+props.lang]}</a>
        <a href="#links" onClick={()=>{props.handlePage('links')}}>{head[21+props.lang]}</a>
         <a href={head[36+props.lang]}>{head[33+props.lang]}</a>
        <a href="#fun" onClick={()=>{props.handlePage('fun')}}>{head[30+props.lang]}</a>
        <a href={head[27+props.lang]}>{head[24+props.lang]}</a>
        </b>
      </div>
      <br/>
      <div>
        <b>
        <a href="#experience" onClick={()=>{props.handlePage('experience')}}>{head[12+props.lang]}</a>
        <a href="#projects" onClick={()=>{props.handlePage('projects')}}>{head[15+props.lang]}</a>
        <a href="#skills" onClick={()=>{props.handlePage('skills')}}>{head[18+props.lang]}</a>
        </b>
      </div>
      <br/>
      <div>
        <b>
        <a href="#education" onClick={()=>{props.handlePage('education')}}>{head[6+props.lang]}</a>
        <a href="#certificates" onClick={()=>{props.handlePage('certificates')}}>{head[9+props.lang]}</a>      
        </b>
      </div>
      </div>
      <br/>
          <div id="lang" class="dropdown">
            <div>
            <b>
            <a href="#language">{props.lang==0?"language":props.lang==1?"言語":props.lang==2?"語":""}</a>  
            </b>
            </div>
            <br/>
            <button class="dropbtn"><b><a href="#selectLang">{props.lang==0?"English":props.lang==1?"日本語":props.lang==2?"中文":""}</a></b></button>
            <div class="dropdown-content">
              {(props.lang!=0)&&<a onClick={()=>{props.handleLang(0)}}>English</a>}
              {(props.lang!=1)&&<a onClick={()=>{props.handleLang(1)}}>日本語</a>}
              {(props.lang!=2)&&<a onClick={()=>{props.handleLang(2)}}>中文</a>}
            </div>
          </div>
      <br/>
      <br/>
    </div>
    </>
  )
}

export default Header;